// Typography
//
// Base font styles for headings and links.

.h1 {
  font-size: 34px;
  line-height: (41 / 34);
  text-transform: uppercase;
  font-weight: 300;
}

.h2 {
  font-size: 25px;
  line-height: 1.42857;
  font-weight: 300;
}

@media (max-width: 890px) {
  .h1 {
    font-size: 20px;
  }

  .h2 {
    font-size: 14px;
  }
}

