.main-gallery-item {
    margin-top: 35px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;

    @media (max-width: 1160px ) {
        height: 230px;
        flex: 0 calc(50% - 19px);
    }

    @media (max-width: 610px ) {
        flex: 0 100%;
        height: 200px;
    }
}
